.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 5px 5px;
  grid-template-areas:
    "pic1 pic1 pic2 pic2"
    "pic4 pic4 pic2 pic2"
    "pic4 pic4 pic3 pic3";
    height:400px;
}

.pic1 { 
  
  background-image: url("./Components/devPort/candy.jpg");
  background-position: center center;
  background-size:cover;
